import { makeAutoObservable } from "mobx";

class CartStore {
  cart = [];

  constructor() {
    makeAutoObservable(this); //  MobX 
  }

  // 
  updateQuantity(itemId, newQuantity) {
    const item = this.cart.find((cartItem) => cartItem.id === itemId);
    if (item) {
      item.quantity = newQuantity;
      item.totalPrice = item.price * newQuantity; // 
    }
  }

  //  addOn
  updateAddOnQuantity(itemId, addOnId, newQuantity) {
    const item = this.cart.find((cartItem) => cartItem.id === itemId);
    if (item) {
      const addOn = item.addOn.find((addOn) => addOn.id === addOnId);
      if (addOn) {
        addOn.quantity = newQuantity;
        addOn.totalPrice = addOn.price * newQuantity; //  addOn
      }
    }
  }

  // 
  addToCart(item) {
    const existingItem = this.cart.find((cartItem) => cartItem.id === item.id);
    if (existingItem) {
      existingItem.quantity += 1; // 
    } else {
      //  addOn  addOn 
      const itemWithAddOn = {
        ...item,
        quantity: 1,
        addOn: item.addOn.map((addon) => ({ ...addon, quantity: 1 })) //  Add-ons
      };
      this.cart.push(itemWithAddOn);
    }
  }

  // 
  removeFromCart(itemId) {
    this.cart = this.cart.filter((item) => item.id !== itemId);
  }

  // 
  clearCart() {
    this.cart = [];
  }

  // 
  get totalPrice() {
    return this.cart.reduce((total, item) => {
      const itemTotal = item.totalPrice || (item.price * item.quantity);
      const addOnTotal = item.addOn.reduce((sum, addOn) => sum + addOn.totalPrice, 0);
      return total + itemTotal + addOnTotal;
    }, 0);
  }

  //  Add-ons 
  get totalAddOns() {
    return this.cart.reduce((total, item) => {
      const addOnCount = item.addOn.reduce((sum, addOn) => sum + addOn.quantity, 0);
      return total + addOnCount;
    }, 0);
  }
}

const cartStore = new CartStore(); //  cartStore  class

export { cartStore };
