import React from "react";
import { Link } from "react-router-dom";
import "./Home.css"; // CSS
//  import Navbar 

const shops = [
  { id: 1, name: "" },
  { id: 2, name: "1" },
  { id: 3, name: " Lover" },
  { id: 4, name: "" },
];

function Home() {
  return (
    <div className="home-container">
      <h1 className="home-title"></h1>
      <div className="shops-list">
        {shops.map((shop) => (
          <Link key={shop.id} to={`/menu/${shop.id}`} className="shop-link">
            {shop.name}
          </Link>
        ))}
      </div>
    </div>
  );
}

export default Home;
