import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { observer } from "mobx-react-lite";
import { cartStore } from "../store/cartstore.js"; //  .js

import "./Cart.css"; //  CSS 

const Cart = observer(() => {
  const [cart, setCart] = useState([]);
  const [error, setError] = useState(""); // 

  //  useEffect  cartStore
  useEffect(() => {
    setCart(cartStore.cart); //  MobX store
  }, [cartStore.cart]); //  cartStore.cart  dependencies

  const calculateTotal = () => {
    return cart.reduce((sum, item) => sum + item.totalPrice, 0);
  };

  // 
  const increaseQuantity = (item) => {
    cartStore.updateQuantity(item.id, item.quantity + 1); //  MobX
  };

  // 
  const decreaseQuantity = (item) => {
    if (item.quantity > 1) {
      cartStore.updateQuantity(item.id, item.quantity - 1); //  MobX
    }
  };

  //  addOn
  const increaseAddOn = (item, addOn) => {
    cartStore.updateAddOnQuantity(item.id, addOn.name, addOn.quantity + 1);
  };

  //  addOn
  const decreaseAddOn = (item, addOn) => {
    if (addOn.quantity > 1) {
      cartStore.updateAddOnQuantity(item.id, addOn.name, addOn.quantity - 1);
    }
  };

  // 
  const handleRemoveItem = (itemId) => {
    cartStore.removeFromCart(itemId); // 
  };

  // 
  const isValidStore = (newItemStore) => {
    if (cart.length === 0) {
      return true; //  
    }
    // 
    const storeName = cart[0].storeName; //  storeName 
    return storeName === newItemStore; // 
  };

  // 
  const handleAddToCart = (item) => {
    if (isValidStore(item.storeName)) {
      cartStore.addToCart(item); // 
    } else {
      setError("");
    }
  };

  return (
    <div className="cart-container">
      <h1 className="cart-header"></h1>
      {error && <p className="error-message">{error}</p>} {/*  */}
      {cart.length === 0 ? (
        <p className="empty-cart"></p>
      ) : (
        <div>
          {cart.map((item, index) => (
            <div key={index} className="cart-item">
              <p>
                <strong>{item.name}</strong> - {item.price} 
              </p>
              {item.addOn && item.addOn.length > 0 && (
                <div className="addOn-section">
                  <h4>Add-ons:</h4>
                  {item.addOn.map((addOn, addOnIndex) => (
                    <div key={addOnIndex} className="addOn-item">
                      <p>
                        <strong>{addOn.name}</strong> - {addOn.price} 
                      </p>
                      <div className="quantity-controls">
                        <button
                          onClick={() => decreaseAddOn(item, addOn)}
                          className="quantity-button"
                        >
                          -
                        </button>
                        <span>{addOn.quantity}</span>
                        <button
                          onClick={() => increaseAddOn(item, addOn)}
                          className="quantity-button"
                        >
                          +
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
              <div className="quantity-controls">
                <button onClick={() => decreaseQuantity(item)} className="quantity-button">
                  -
                </button>
                <span>{item.quantity || 1}</span>
                <button onClick={() => increaseQuantity(item)} className="quantity-button">
                  +
                </button>
              </div>
              <p>: {item.totalPrice} </p>

              {/*  */}
              <button
                onClick={() => handleRemoveItem(item.id)}
                className="remove-item-button"
              >
                
              </button>
            </div>
          ))}
          <p className="cart-total">: {calculateTotal()} </p>
        </div>
      )}

      {/*  */}
      <Link to="/payment">
        <button className="checkout-button"></button>
      </Link>
    </div>
  );
});

export default Cart;
