import React, { useState } from "react";
import { observer } from "mobx-react-lite";
import { cartStore } from '../store/cartstore.js';
import { QRCodeSVG } from 'qrcode.react';  //  QRCodeSVG
import './Payment.css';  //  CSS

const Payment = observer(() => {
  const [paymentProof, setPaymentProof] = useState(null); // 

  const totalPrice = cartStore.cart.reduce((acc, item) => acc + item.totalPrice, 0);

  const handlePayment = () => {
    if (!paymentProof) {
      alert("");
      return;
    }
    alert("!");
    cartStore.clearCart(); // 
  };

  const qrCodeData = `totalPrice=${totalPrice}`;

  // 
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setPaymentProof(file); // 
    }
  };

  return (
    <div className="payment-container">
      <h1></h1>
      {cartStore.cart.length > 0 ? (
        <div>
          <ul>
            {cartStore.cart.map((item, index) => (
              <li key={index}>
                {item.name} - {item.totalPrice} 
              </li>
            ))}
          </ul>
          <h3>: {totalPrice} </h3>

          <div className="qr-container">
            <h4>QR Code :</h4>
            <QRCodeSVG value={qrCodeData} size={256} className="qr-code" />
          </div>

          {/*  */}
          <div className="payment-proof">
            <label htmlFor="paymentProof">:</label>
            <input
              type="file"
              id="paymentProof"
              onChange={handleFileChange}
              accept="image/*"
            />
            {paymentProof && (
              <div className="proof-preview">
                <p>: {paymentProof.name}</p>
                <img
                  src={URL.createObjectURL(paymentProof)}
                  alt=""
                  className="proof-image"
                />
              </div>
            )}
          </div>

          <button onClick={handlePayment}></button>
        </div>
      ) : (
        <p className="empty-cart-message"></p>
      )}
    </div>
  );
});

export default Payment;
